.icon-left {
	position: absolute;
	top: 10px;
	left: 10px;
}

.icon-right {
	position: absolute;
	bottom: 10px;
	right: 10px;
}

.season-display.winter i {
	color: blue;
}

.season-display.summer i {
	color: red;
}

.season-display {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	height: 100vh;
}

.winter {
	background-color: aliceblue;
}

.summer {
	background-color: orange;
}



